    font-size: 140px;
    font-weight: 700;
    line-height: 140px
}

.http-error h4 {
    color: #777
}

.http-error p {
    font-size: 1.4em;
    line-height: 36px
}

@media(max-width:575px) {
    .http-error {
        padding: 0
    }

    .http-error h2 {
        font-size: 100px;
        letter-spacing: 0;
        line-height: 100px
    }
}